.hero-container{
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items:center;  
    margin-bottom: -40px;
    background-image: url('https://images.unsplash.com/photo-1582574643306-d00ea3f7d49b?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=2100&q=80');
}

.hero-container > h1 {
    color: #fff;
    font-size: 100px;
    margin-top: -100px;
    font-weight: 200;   
}

.hero-container > p {
    margin-top: 8px;
    color: #fff;
    font-weight: 200;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 32px;
}

.hero-btns {
    margin-top: 32px;
}

.hero-btns .btn{
    margin: 6px;
}

.fa-play-circle {
    margin-left: 4px;
}

@media screen and (max-width: 960px){
    .hero-container > h1 {
        font-size: 70px;
        margin-top: -150px;
    }
}

@media screen and (max-width: 768px) {
    .hero-container > h1 {
        font-size: 50px;
        margin-top: -100px;
    }

    .hero-container >p {
        font-size: 30px;
    }

    .btn-mobile {
        display: block;
        text-decoration: none;
    }

    .btn {
        width:100%;
    }
}

@media screen and (max-width: 380px) {
    .hero-container > h1 {
        font-size: 40px;
        margin-top: 0px;
    }
    .hero-container >p {
        font-size: 20px;
    }

    .hero-btns {
        margin-top: 220px;
    }
    
    .hero-btns .btn{
        margin: 6px;
    }
}