  }
  
  .card__details ul li {
    text-align: center;
    font-size: 1.3rem;
    padding: 1rem; 
  }
  
  .card__details ul li:not(:last-child) {
      border-bottom: 1px solid #eee; 
  }
  
  .card__cta {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
